import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {
  ScreenshotSharingService
} from '@nequi/apps/base/mobile/config/features/commons/services/screenshot-sharing';
import { AnalyticsInteractor, AnalyticsType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { IconToastType, ToastPosition, ToastService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { LoanInfoConfig } from './loan-info.config';
import { LoanInfoViewModel } from './loan-info.view-model';

@Component({
  selector: 'app-loan-info-credits',
  templateUrl: './loan-info.page.html',
  styleUrls: ['./loan-info.page.scss'],
})
export class LoanInfoPage extends FeatureBasePage implements OnInit {
  public viewModel: LoanInfoViewModel;
  public config = LoanInfoConfig;

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private toastService: ToastService,
    private creditInfoModel: CreditInfoModel,
    private route: ActivatedRoute,
    private screenshotSharingService: ScreenshotSharingService,
    private analyticsInteractor: AnalyticsInteractor
  ) {
    super(injector);

    this.viewModel = new LoanInfoViewModel();
  }

  public async ngOnInit() {
    this.showSaveToast();
    this.initData();
  }

  public async shareScreenshoot() {
    await this.screenshotSharingService.viaOptions();
  }

  public async saveScreenshot() {
    await this.screenshotSharingService.viaOptions();
  }

  public goKnowUsBetter(): void {
    this.setAnalyticsIntentionOfSummary();

    this.navigatorProvider.navigateForward(this.config.routes.knowUsBetter, {
      relativeTo: this.route,
    });
  }

  private initData(): void {
    const p100 = 100;

    this.viewModel.commissionPercentage =
      this.creditInfoModel.creditFeatureInfo.commissionPercentage / p100;

    this.viewModel.loanAmount = this.creditInfoModel.creditFeatureInfo.loanAmount;
    this.viewModel.monthlyFee = this.creditInfoModel.creditFeatureInfo.monthlyFee;
    this.viewModel.monthlyInsuranceAmount = this.creditInfoModel.loanPortfolioInfo.monthlyInsuranceAmount;
    this.viewModel.monthlyInterestRate =
      this.creditInfoModel.loanPortfolioInfo.monthlyInterestRate / p100;

    this.viewModel.normalAnualEfectiveRate =
      this.creditInfoModel.loanPortfolioInfo.normalAnualEfectiveRate / p100;

    this.viewModel.installment = this.creditInfoModel.creditFeatureInfo.installment;
    this.viewModel.expirationDateText = this.creditInfoModel.creditFeatureInfo.expirationDateText;
  }

  private async showSaveToast(): Promise<void> {
    const message = this.translateProvider.get(this.config.i18n.loanInfo.toatMessage);

    await this.toastService.showWithIcon({
      message,
      type: IconToastType.INFO,
      position: ToastPosition.TOP,
      cssClass: this.config.toast.cssClass,
      isBackdropDismiss: true,
    });
  }

  private setAnalyticsIntentionOfSummary() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfSummary.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfSummary.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfSummary.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfSummary.params.cleverTap,
          [getFormattedDateTimes(), this.viewModel.loanAmount, this.viewModel.installment]
        ),
      },
    });
  }
}
