import { Component, Injector, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import {
  AnalyticsInteractor,
  AnalyticsType,
  Attribute,
  LocationEntity,
  LoggerInteractor,
  ModuleCode,
  ModuleInfoResponseEntity,
  ModuleName,
  OccupationEntity,
  ParameterInteractor,
  Transaction
} from '@nequi/features/commons/core';
import { TransactionBasePage } from '@nequi/features/commons/presentation';
import {
  CalendarParametersEntity,
  CreditEventsHandler,
  CreditType,
  DisbursementAdditionalInfo,
  DisbursementEntity,
  DisbursementTransaction,
  OperationCreditType
} from '@nequi/features/credits/core';
import { ModuleInfoInteractor } from '@nequi/features/profile/core';
import { CalendarOptions, CalendarParams, CalendarPlugin } from '@nequi/native/calendar';
import { CardModalService } from '@nequi/ui';
import { getFormattedDateTimes } from '@nequi/utils/date';
import { stringsArrayToObjectWithValues } from '@nequi/utils/functions';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CustomerFinancialService } from '../../../../services/customer-finacial.service';
import { AgreementFGAConfig } from './agreement-fga.config';

@Component({
  selector: 'app-credits-lifeguard-recharged-agreement-fga',
  templateUrl: './agreement-fga.page.html',
})
export class AgreementFgaPage extends TransactionBasePage implements OnInit {
  public config = AgreementFGAConfig;
  public acceptAgreementCheckbox: FormControl;
  public agreementFga: string;

  private calendarMessages: Record<string, string>;

  constructor(
    protected readonly injector: Injector,
    private readonly route: ActivatedRoute,
    private creditInfoModel: CreditInfoModel,
    private analyticsInteractor: AnalyticsInteractor,
    private customerFinancialService: CustomerFinancialService,
    private moduleInfoInteractor: ModuleInfoInteractor,
    private cardModalService: CardModalService,
    private creditEventsHandler: CreditEventsHandler,
    private calendarPlugin: CalendarPlugin,
    private loggerInteractor: LoggerInteractor,
    private parameterInteractor: ParameterInteractor
  ) {
    super(injector);
    this.calendarMessages = this.translateProvider.get(this.config.i18n.calendar);
  }

  public ngOnInit(): void {
    this.initData();
  }

  public async acceptAgreement(): Promise<void> {
    this.setAnalyticsIntentionOfEnd();
    this.disbursement();
  }

  protected async setOriginMoney(transaction: Transaction): Promise<Transaction> {
    return transaction;
  }

  protected async validatePin() {
    const pinMessages = this.translateProvider.get(this.config.i18n.pinChallengeMessage);

    return this.pinChallengeModalService.show({
      title: pinMessages.title,
      message: pinMessages.message,
      isOptional: true,
    });
  }

  protected async onTransactionSuccess() {
    this.setAnalyticsSuccess();
    await this.createCalendarEvents();
    this.showCardModal();

    this.creditEventsHandler.emitDisbursedEvent({
      creditType: CreditType.LIFEGUARD_RECHARGED,
      hasCredits: true,
      isExpired: false,
      isRescheduled: false,
    });
  }

  private async disbursement() {
    const data = await this.getUserInfo();

    const params: DisbursementEntity = {
      operatorType: OperationCreditType.DISBURSEMENT,
      isDebitType: true,
      approvedAt: this.creditInfoModel.creditFeatureInfo.approvedDate,
      loanValue: this.creditInfoModel.creditFeatureInfo.loanAmount,
      productId: CreditType.LIFEGUARD_RECHARGED,
      gender: this.customerFinancialService.creditUserInfo.gender,
      purpose: '',
      additionalInfo: {
        city: data.city || '',
        state: data.state || '',
        address: data.address || '',
        installment: this.creditInfoModel.creditFeatureInfo.installment,
        ciiu: data.ciiu || '',
        totalAssets: this.customerFinancialService.creditUserInfo.assets
          ? this.customerFinancialService.creditUserInfo.assets.toString()
          : '',
        fee: this.creditInfoModel.creditFeatureInfo.installment,
      } as DisbursementAdditionalInfo,
    };

    const transaction = new DisbursementTransaction<DisbursementEntity>().withData(params);

    super.executeTransaction(transaction);
  }

  private loadModule<T>(
    moduleInfoList: ModuleInfoResponseEntity[],
    moduleCode: ModuleCode,
    moduleName: ModuleName
  ) {
    const moduleInfo = moduleInfoList.find((module) => module.code === moduleCode);

    return moduleInfo?.data?.find((detail) => detail.key === moduleName)?.value as T;
  }

  private async getUserInfo() {
    try {
      await this.loadingService.show();
      const data = await this.moduleInfoInteractor.getModuleInfo([
        ModuleCode.LOCATION,
        ModuleCode.OCCUPATION,
      ]);

      const locationData = this.loadModule<LocationEntity>(
        data,
        ModuleCode.LOCATION,
        ModuleName.LOCATION
      );

      const occupationData = this.loadModule<OccupationEntity>(
        data,
        ModuleCode.OCCUPATION,
        ModuleName.OCCUPATION
      );

      await this.loadingService.hideWithoutFeedback();

      return {
        city: locationData.city.code.toString(),
        state: locationData.province.code.toString(),
        address: locationData.address,
        ciiu: occupationData.ciiuCode?.key ? occupationData.ciiuCode.key.toString() : '',
      };
    } catch (error) {}
  }

  private async showCardModal(): Promise<void> {
    const i18n = this.translateProvider.get(this.config.i18n.cardModalMessage);

    this.cardModalService.show({
      title: i18n.title,
      imgPath: i18n.image,
      content: i18n.content,
      buttons: [
        {
          text: i18n.button,
          handler: () => {
            this.goToBackpack();
          },
        },
      ],
    });
  }

  private goToBackpack() {
    this.navigatorProvider.navigateForward(this.config.routes.backpack, {
      relativeTo: this.route,
    });
  }

  private initData(): void {
    const [year, month, day] = new Date().toISOString().split(this.config.regexDate);
    const contractHTML = this.creditInfoModel.agreement.agreementFGA;

    this.agreementFga = contractHTML
      .replace(this.config.replaceString.suscribeDay, day)
      .replace(this.config.replaceString.suscribeMonth, month)
      .replace(this.config.replaceString.suscribeYear, year);
  }

  private setAnalyticsIntentionOfEnd() {
    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: this.config.analytics.events.intentionOfEnd.name,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: this.config.analytics.events.intentionOfEnd.name },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.CLEVERTAP,
      params: {
        eventName: this.config.analytics.events.intentionOfEnd.name,
        eventProps: stringsArrayToObjectWithValues(
          this.config.analytics.events.intentionOfEnd.params.cleverTap,
          [
            getFormattedDateTimes(),
            this.creditInfoModel.creditFeatureInfo.loanAmount,
            this.creditInfoModel.creditFeatureInfo.term.value,
            this.creditInfoModel.creditFeatureInfo.loanDestination,
          ]
        ),
      },
    });
  }

  private setAnalyticsSuccess() {
    const [loanSuccess, mobileSubscribe] = this.config.analytics.events.success.name;

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: loanSuccess,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FACEBOOK,
      params: {
        eventName: mobileSubscribe,
      },
    });

    this.analyticsInteractor.trackEvent({
      type: AnalyticsType.FIREBASE,
      params: { eventName: loanSuccess },
    });
  }

  private async createCalendarEvents() {
    try {
      let startHour = this.config.calendar.startHour;
      let endHour = this.config.calendar.endHour;
      const parameters = await this.getDynamoCalendarParameters();

      if (parameters) {
        startHour = parameters.startHour;
        endHour = parameters.endHour;
      }

      const currentDate = new Date();
      const startDate = new Date(
        currentDate.getFullYear(),
        currentDate.getMonth() + 1,
        currentDate.getDate()
      );
      const options = await this.getCalendarOptions();

      const params: CalendarParams = {
        title: this.calendarMessages.title,
        location: null,
        description: this.translateProvider.get(this.config.i18n.calendarDescription, {
          deepLink: this.calendarMessages.url,
        }),
        startDate: new Date(startDate.setHours(startHour, 0, 0, 0)),
        endDate: new Date(startDate.setHours(endHour, 0, 0, 0)),
        options,
      };

      await this.calendarPlugin.createEvent(params);
    } catch (error) {
      this.loggerInteractor.error(`[calendar-plugin] ${JSON.stringify(error)}`);
    }
  }

  private async getCalendarOptions(): Promise<CalendarOptions> {
    const options = await this.calendarPlugin.getCalendarOptions();
    const parameters = await this.getDynamoCalendarParameters();

    if (parameters) {
      options.firstReminderMinutes = parameters.firstReminderMinutes;
      options.secondReminderMinutes = parameters.secondReminderMinutes;
    } else {
      options.firstReminderMinutes = this.config.calendar.firstReminderMinutes;
      options.secondReminderMinutes = this.config.calendar.secondReminderMinutes;
    }

    return options;
  }

  private async getDynamoCalendarParameters(): Promise<CalendarParametersEntity> {
    return (
      await this.parameterInteractor.getParameter<Attribute[]>(this.config.calendar.param)
    ).reduce((all, param) => {
      return {
        ...all,
        [param.code]: Number(param.value),
      };
    }, {} as CalendarParametersEntity);
  }
}
