import { Injectable } from '@angular/core';
import {
  CONFIDENCE_SCORE_CREDITS_ERRORS,
  CreditType,
  CUSTOMER_MUST_GO_TO_ENGINE,
  CustomerCreditsEntity,
  DontHaveApprovalAttemptException,
  DontHaveApprovalAttemptWithDataException,
  ErrorCode,
  HaveApprovalAttemptException,
  ValidateCreditsParamsException,
  ValidateCreditTypeException
} from '@nequi/features/credits/core';

import { BaseMiddleware } from './base.middleware';
import { ModalParams } from './modal.params';

@Injectable()
export class LifeguardRechargedMiddleware extends BaseMiddleware {
  protected async getCustomerCreditsFail(error): Promise<boolean> {
    this.dynatraceService.setCustomError(error);

    let result = false;

    if (CUSTOMER_MUST_GO_TO_ENGINE.includes(error.code)) {
      if (error.data) {
        this.setCustomerCreditsError(error.data);
      }

      result = await this.showWelcome();

      return result ? this.showUserDontHavePreapprovalModal() : result;
    }

    if (CONFIDENCE_SCORE_CREDITS_ERRORS.includes(error.code)) {
      await this.onSarlaftLocksError();

      return;
    }

    if (error.code === ErrorCode.EXPLORE_THE_SEA) {
      result = await this.showWelcome();

      return result ? this.showOtherCreditOptions(error.data) : result;
    }

    if (error.code === ErrorCode.WRONG_TIME_DISBURSEMENT_CREDIT) {
      this.toastService.showError(error.message);

      return false;
    }

    // TODO: Queda pendiente un refactor para incluir los errores anteriores (No se hace para evitar regresion en el HU)
    if (this.getCustomerCreditException[error.constructor.name]) {
      return this.getCustomerCreditException[error.constructor.name](error);
    }

    await this.showComeBackLaterAlert(error.message);

    return result;
  }

  protected setGetCustomerCreditsException() {
    this.getCustomerCreditException = {
      [ValidateCreditTypeException.name]: async () =>
        !this.navController.navigateForward(this.config.routes.lifeguard),
      [ValidateCreditsParamsException.name]: async () => this.onCreditsParamsError(),
    };
  }

  protected async showOtherCreditOptions(data: CustomerCreditsEntity): Promise<boolean> {
    let i18nKey = this.config.lifeguardRecharged.modal.lifeguard;
    let url: string;

    const [creditType] = data?.availableProducts;

    if (creditType === CreditType.PROPELLER) {
      i18nKey = this.config.lifeguardRecharged.modal.propeller;
      url = this.config.routes.propellerPreapproved;
    }

    return this.showCardModal<boolean>({
      i18nKey: i18nKey,
      onAccept: () => (url ? this.navController.navigateRoot(url) : false),
      onCancel: () => false,
    } as ModalParams);
  }

  protected async failApprovalUpFrontAttempt(error): Promise<boolean> {
    this.dynatraceService.setCustomError(error);

    if (error?.data) {
      this.setMaxAttemptApprovalFront(error.data);
    }

    if (error.constructor === HaveApprovalAttemptException) {
      this.navController.navigateRoot(this.config.lifeguardRecharged.routes.knowUsBetterEngine);
    }

    if (error.constructor === DontHaveApprovalAttemptWithDataException) {
      return true;
    }

    if (error.constructor === DontHaveApprovalAttemptException) {
      this.processGetApprovalAttemptError(error);
    }

    if (error.constructor !== HaveApprovalAttemptException) {
      await this.toastService.showError(error.message);
    }

    return false;
  }

  private async onCreditsParamsError(): Promise<boolean> {
    const i18n = this.translateProvider.get(this.config.common.alert.paramsError);

    this.alertService.show({ messages: i18n });

    return false;
  }
}
