import { Component, Injector } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FeatureBasePage } from '@nequi/features/commons/presentation';

import { ItsSeriousConfig } from './its-serious.config';

@Component({
  selector: 'app-credits-its-serious',
  templateUrl: './its-serious.page.html',
})
export class ItsSeriousPage extends FeatureBasePage {
  public config = ItsSeriousConfig;

  constructor(injector: Injector, private route: ActivatedRoute) {
    super(injector);
  }

  public goToTermsAndConditions() {
    this.navigatorProvider.navigateForward(this.config.goTo.termsAndConditions, {
      relativeTo: this.route,
    });
  }
}
