import { CurrencyPipe } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FlowType } from '@nequi/features/commons/core';
import { FeatureBasePage } from '@nequi/features/commons/presentation';
import { TranslateProvider } from '@nequi/features/commons/providers';
import { CreditInteractor, CreditType, LoanPortfolioEntity } from '@nequi/features/credits/core';
import {
  ButtonColor,
  InfoListBottomSheetService,
  InfoListItem,
  LoadingService,
  Theme,
  ToastService
} from '@nequi/ui';
import { openUrl, toCamelCaseProperties } from '@nequi/utils/functions';
import { createFormat } from '@nequi/utils/string';

import { CreditInfoModel } from '../../../../services/credit-info.model';
import { CreditLoggerService } from '../../../../services/credit-logger.service';
import { EnsureLoanConfig } from './ensure-loan.config';

@Component({
  selector: 'app-credits-lifeguard-recharged-ensure-loan',
  templateUrl: './ensure-loan.page.html',
  styleUrls: ['./ensure-loan.page.scss'],
})
export class EnsureLoanPage extends FeatureBasePage implements OnInit {
  public config = EnsureLoanConfig;
  public ensureLoanWithNequiTitle: string;
  public loanPortfolio: LoanPortfolioEntity;

  private bottomSheetI18n: { [index: string]: any };

  constructor(
    injector: Injector,
    private translateProvider: TranslateProvider,
    private infoListBottomSheetService: InfoListBottomSheetService,
    private route: ActivatedRoute,
    private creditInteractor: CreditInteractor,
    private creditInfoModel: CreditInfoModel,
    private creditLoggerService: CreditLoggerService,
    private loadingService: LoadingService,
    private toastService: ToastService,
    private currencyPipe: CurrencyPipe
  ) {
    super(injector);
  }

  public ngOnInit() {
    this.initPage();
  }

  public goToLoanInfo() {
    this.navigatorProvider.navigateForward(this.config.goTo.loanInfo, {
      relativeTo: this.route,
    });
  }

  public async showMoreInfo() {
    const insuranceWithNequi = this.bottomSheetI18n.insuranceWithNequi;

    const itemListBottomSheet: InfoListItem[] = Object.values(insuranceWithNequi.itemList);

    const buttons = [
      {
        id: this.config.idSelectors.continueButtonId,
        label: insuranceWithNequi.footer.continueWithNequiButton,
        color: ButtonColor.GUAYABA,
        action: async () => {
          await this.infoListBottomSheetService.hide();
          this.goToLoanInfo();
        },
      },
    ];

    this.infoListBottomSheetService.show({
      title: insuranceWithNequi.header.title,
      items: itemListBottomSheet,
      buttons,
      theme: Theme.LIGHT,
    });
  }

  public async showHaveAnotherInsurance() {
    const haveAnotherInsurance = this.bottomSheetI18n.haveAnotherInsurance;

    const itemListBottomSheet: InfoListItem[] = Object.values(haveAnotherInsurance.itemList);

    const buttons = [
      {
        id: this.config.idSelectors.chooseButtonId,
        label: haveAnotherInsurance.footer.chooseTheOneFromNequiButton,
        color: ButtonColor.GUAYABA,
        action: async () => {
          await this.infoListBottomSheetService.hide();
          this.goToLoanInfo();
        },
      },
      {
        id: this.config.idSelectors.policyButtonId,
        label: haveAnotherInsurance.footer.sendMailWithPolicyButton,
        color: ButtonColor.MEDIANOCHE,
        action: () => {
          openUrl(this.config.goTo.sendYourPolicy);
        },
      },
    ];

    this.infoListBottomSheetService.show({
      title: haveAnotherInsurance.header.title,
      items: itemListBottomSheet,
      buttons,
      theme: Theme.LIGHT,
    });
  }

  private async initPage() {
    try {
      await this.loadingService.show();

      this.loanPortfolio = await this.creditInteractor.getLoanPortfolio({
        creditType: CreditType.LIFEGUARD_RECHARGED,
        amount: this.creditInfoModel.creditFeatureInfo.loanAmount,
        numInstallments: this.creditInfoModel.creditFeatureInfo.installment,
      });

      await this.loadingService.hide(true);

      this.creditInfoModel.loanPortfolioInfo = this.loanPortfolio;

      this.initItemList();
    } catch (error) {
      await this.loadingService.hide(false);
      await this.toastService.showError(error.message);
    }
  }

  private async initItemList(): Promise<void> {
    const i18n = this.translateProvider.get(this.config.i18n.ensureLoan.content);
    const i18nFormatted = toCamelCaseProperties<any>(i18n);

    this.saveLog();

    this.bottomSheetI18n = i18nFormatted.bottomSheet;
    const itemList = this.bottomSheetI18n.insuranceWithNequi.itemList;

    itemList.whatItCostsYou.description = createFormat(itemList.whatItCostsYou.description, [
      this.currencyPipe.transform(this.loanPortfolio.monthlyInsuranceAmount.toString()),
    ]);

    itemList.whoIsMyInsuranceWith.description = createFormat(
      itemList.whoIsMyInsuranceWith.description,
      [this.config.insuranceCarrier]
    );

    this.ensureLoanWithNequiTitle = createFormat(i18nFormatted.ensureLoanWithNequi.title, [
      this.currencyPipe.transform(this.loanPortfolio.monthlyInsuranceAmount.toString()),
    ]);

    this.bottomSheetI18n.insuranceWithNequi.itemList = itemList;
  }

  private async saveLog() {
    this.creditLoggerService.save({
      flowType: FlowType.LIFEGUARD_RECHARGED,
      i18nKey: this.config.i18n.logs,
      params: [this.currencyPipe.transform(this.loanPortfolio.monthlyInsuranceAmount)],
    });
  }
}
