import { CurrencyPipe, DatePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {
  ScreenshotSharingService
} from '@nequi/apps/base/mobile/config/features/commons/services/screenshot-sharing';
import { NavigatorProvider, TranslateProvider } from '@nequi/features/commons/providers';
import { CreditEntity } from '@nequi/features/credits/core';
import { ViewDetailItem } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { PaymentDetailConfig } from './payment-detail.config';

@Component({
  selector: 'app-credits-lifeguard-recharged-payment-detail',
  templateUrl: './payment-detail.page.html',
  styleUrls: ['./payment-detail.page.scss'],
})
export class PaymentDetailPage implements OnInit {
  public config = PaymentDetailConfig;
  public isScreenShotTaken = false;
  public detailItem: Array<ViewDetailItem>;

  constructor(
    private navigatorProvider: NavigatorProvider,
    private translateProvider: TranslateProvider,
    private screenshotSharingService: ScreenshotSharingService,
    private router: Router,
    private currencyPipe: CurrencyPipe,
    private datePipe: DatePipe
  ) {}

  public async ngOnInit() {
    const credit = (await this.router.getCurrentNavigation().extras.state) as CreditEntity;

    this.loadDetail(credit);
  }

  public goBack(): void {
    this.navigatorProvider.back();
  }

  public async shareScreenshoot() {
    this.isScreenShotTaken = true;
    await this.screenshotSharingService.viaOptions();
    this.isScreenShotTaken = false;
  }

  public async saveScreenshot() {
    await this.screenshotSharingService.viaOptions();
  }

  private async loadDetail(credit: CreditEntity) {
    if (credit.isExpired) {
      this.buildExpiredItems(credit);

      return;
    }

    this.buildDefaultItems(credit);
  }

  private buildDefaultItems(credit: CreditEntity) {
    const items = this.translateProvider.get(this.config.i18n.paymentDetail);

    this.detailItem = [
      {
        label: items.loanValue,
        description: this.currencyPipe.transform(credit.approvedValue),
      },
    ];

    this.interestRates(credit, items.interestRates);

    this.detailItem = this.detailItem.concat([
      {
        label: items.monthlyFee.label,
        description: this.currencyPipe.transform(credit.normalInterest),
        helpMessage: items.monthlyFee.message,
      },
      {
        label: items.monthlyInsurance.label,
        description: this.currencyPipe.transform(credit.feesOutstanding),
        helpMessage: createFormat(items.monthlyInsurance.message, [
          this.currencyPipe.transform(credit.feesOutstanding),
        ]),
      },
      {
        label: items.totalvalue,
        description: this.currencyPipe.transform(credit.balance),
      },
      {
        label: items.paydayLimit,
        description: this.datePipe.transform(new Date(credit.endAt)),
      },
      {
        label: items.numberContract,
        description: credit.debenture,
      },
    ]);
  }

  private buildExpiredItems(credit: CreditEntity) {
    const items = this.translateProvider.get(this.config.i18n.paymentDetailExpired);

    this.detailItem = [
      {
        label: items.loanValue,
        description: this.currencyPipe.transform(credit.approvedValue),
      },
    ];

    this.interestRates(credit, items.interestRates);

    this.detailItem = this.detailItem.concat([
      {
        label: items.monthlyFee.label,
        description: this.currencyPipe.transform(credit.normalInterest),
      },
      {
        label: items.monthlyFeeArrears.label,
        description: this.currencyPipe.transform(credit.penaltyInterest),
        helpMessage: items.monthlyFeeArrears.message,
      },
      {
        label: items.monthlyInsuranceToday.label,
        description: this.currencyPipe.transform(credit.feesOutstanding),
        helpMessage: createFormat(items.monthlyInsuranceToday.message, [
          this.currencyPipe.transform(credit.feesOutstanding),
          // TODO: dudas de merge, toca validar cual dato es
          // this.currencyPipe.transform(this.config.insurance),
        ]),
      },
      {
        label: items.totalvalue,
        description: this.currencyPipe.transform(credit.balance),
      },
      {
        label: items.paydayLimit,
        description: this.datePipe.transform(new Date(credit.endAt)),
      },
      {
        label: items.numberContract,
        description: credit.debenture,
      },
    ]);
  }

  private interestRates(
    credit: CreditEntity,
    interestRates: { label: string; description: string; message: string }
  ) {
    if (credit.rateAnnualInterest && credit.rateMonthlyInterest) {
      this.detailItem.push({
        label: interestRates.label,
        description: createFormat(interestRates.description, [
          credit.rateMonthlyInterest.toString(),
          credit.rateAnnualInterest.toString(),
        ]),
        helpMessage: interestRates.message,
      });
    }
  }
}
